.main-tutorials {
  min-height: 100vh;
  height: 100% !important;
  display: flex;
  width: 100%;
  justify-content: space-around;
  flex-direction: row-reverse;
  align-items: flex-start;
  padding: 2rem 6rem;
  background-image: url('../../assets/image/AdobeStock_331719856.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;
}
.tutorial {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.tutorials-title {
  color: #ff0038;
  display: flex;
  align-items: center;
}
h2 {
  color: var(--color);
  margin: 0;
}

@media screen and (max-width: 80em) {
  .title {
    font-size: 4rem;
  }
  .subTitle {
    font-size: 1.2rem;
  }
}
@media screen and (max-width: 70em) {
}

@media screen and (max-width: 56em) {
  .main-tutorials {
    padding: 1rem;
  }
  .descritif-tutorials {
    margin-left: 0;
    grid-column: 1/1;
  }

		.descritif-tutorials h1 {
    font-size: 40px;
}
	.descritif-tutorials strong {
    font-size: 40px;
}
	.descritif-tutorials p {
    font-size: 16px;
}
}

@media screen and (max-width: 37.5em) {
  .main-tutorials {
    padding: 1rem;
  }
  .descritif-tutorials {
    margin-left: 0;
    grid-column: 1/1;
  }
}

